@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css?family=Open+Sans&display=swap');
:root{
    --main-font-family-1: 'impact', sans-serif;
    --main-font-family-2: 'lora', sans-serif;
    --main-font-family-3: 'nunito' sans-serif;
    --fa-spotify-color: green;
}
body{
    background: rgb(209,0,255);
    background: -moz-linear-gradient(126deg, rgba(209,0,255,1) 0%, rgba(255,255,255,1) 96%);
    background: -webkit-linear-gradient(126deg, rgba(209,0,255,1) 0%, rgba(255,255,255,1) 96%);
    background: linear-gradient(126deg, rgba(209,0,255,1) 0%, rgba(255,255,255,1) 96%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#d100ff",endColorstr="#ffffff",GradientType=1);
}


header{
	margin: 0px;
	width: 100%;
	height: 70px;
	display: flex;
    align-items: center; 
	justify-content:right; 
    background: #9b33b1;
    opacity: 0.7;
	position: fixed;
	top: 0;
    left: 0;
	transition: 0.3s;
	z-index: 5;
}
header:hover{
	opacity: 1;
	background: #9b33b1;;
}


