.pomodoro{
    padding:30px;
    display:flex;
}

.nav-bar{
	list-style:none;
	position: relative;
	display: inline-flex;
}

body {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: flex;
    --bg:#613594;
    background-color:var(--bg);
    color:white;
}

.player {
    width:640px;
    background-color:var(--bg);
    display: grid;
    grid-template-columns: 48px 1fr 48px;
    grid-gap:16px;
    padding:8px 24px;
    position:relative;
    border-radius: 64px;

}

.control,#current {
    height: 100%;
    display:flex;
    justify-content: center;
    align-items: center;
    padding-block: 16px;

}

#playbtn{
    font-size: 24px;
    color:transparent;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: white;
    cursor: pointer;  
}

#playbtn:hover{
    -webkit-text-fill-color: white;
}

.info{
    display:flex;
    justify-content: center;
    align-items: center;
    font-size: 21px;
}

#current {
    text-align: right;
}
.bar {
    --space: -4px;
    background-color:#24242424;
    overflow: hidden;
    left: var(--space);
    right: var(--space);
    top: var(--space);
    bottom: var(--space);
    position: absolute;
    border-radius: 64px;
    z-index:-2;
}

#progress {
    display: bolck;
    width: var(--progress, 0%);
    background-color: white;
    transition: all 1s linear;
    height: 100%;
}
